.divider_text_container {
    display: flex;
    flex-direction: row;
    gap: 32px;
    height: 20px;


    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #BEBEBE;
    align-items: center;
}

.divider {
    border: none;
    outline: none;
    height: 2px;
    background: #EDEDED;

    margin: 1px 0;
}