.NavbarButton_container {
    display: flex;
    justify-content: row;
    width: 100%;
}

.btn-subject:hover {
    margin-left: 8px;
    transition: margin 0.2s;
    border-left: 1px solid #d1d1d123;
}


.btn-subject {

    align-items: center;

    color: white;
    padding: 14.5px 16px 14.5px 16px;

    font-size: 16px;
    font-weight: 400;
    line-height: 27px;
    letter-spacing: 0em;
    text-align: left;


    height: auto;
    width: 100%;

    background: none;

    border-right: none;
    color: white;
    transition: margin 0.2s;


}

.btn-subject-active {

    align-items: center;

    color: white;
    padding: 14.5px 16px 14.5px 16px;

    font-size: 16px;
    font-weight: 500;
    line-height: 27px;
    letter-spacing: 0em;
    text-align: left;


    height: auto;
    width: 100%;

    background: #8f8ad915;

    border-right: 4px solid #8F8AD9;
    color: #8F8AD9;
    transition: margin 0.2s;

}

