.Placeholder_container
{
    width: 90%;
    margin-top:42px;
    display: flex;
    margin: auto;
    justify-content: center;
    flex-direction: column;
    align-items: center;

}

.Heading_container
{
width: 100%;
display: flex;
flex-direction: column;
gap: 16px;
}



.Subheading_container
{
    display: flex;
    flex-direction: row;
    gap: 8px;
    align-items: baseline;
    text-align: center;
    justify-content: center;
}
.Heading_text
{
    display: block;
    font-weight: 500;
    font-size: 36px;
    line-height: 50px;
    color: #1D1D1D;
    text-align: center;
}
.Subheading_text
{
    display: block;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #bebebe;
}

.Placeholder_image
{
    width: 100%;
    max-height: 780px;
    opacity: 20%;
    min-width: 500px;
    -webkit-filter: grayscale(100%); /* Safari 6.0 - 9.0 */
    filter: grayscale(100%);
}


.Placeholder_image__big
{
    width: 100%;
    max-height: 780px;
    opacity: 20%;
    max-width: max-content;
}
