.code_display {

    font-weight: 600;
    background: #8F8AD9;
    border-bottom: 6px solid #807CBF;


    padding: 25px 75px;


    font-weight: 600;
    font-size: 48px;
    line-height: 60px;

    text-align: center;
    letter-spacing: 0.085em;

    color: white;
    width: 100%;

    box-shadow: 0px -2px 22px rgba(143, 138, 217, 0.4), 0px -0.835552px 9.19107px rgba(143, 138, 217, 0.287542), 0px -0.446726px 4.91399px rgba(143, 138, 217, 0.238443), 0px -0.250431px 2.75474px rgba(143, 138, 217, 0.2), 0px -0.133002px 1.46302px rgba(143, 138, 217, 0.161557), 0px -0.0553451px 0.608796px rgba(143, 138, 217, 0.112458);
    transition: all 0.2s;
}

.code_display:hover {
    border-bottom: 12px solid #807CBF;
    background: #8b86d3;
    letter-spacing: 0.15em;
    /* font-size: 52px; */
    /* transform: scale(1.05); */
    transition: all 0.2s;
}


.code_display::before
{
    opacity: 50%;
    margin: auto;
    width: 100%;
    white-space: nowrap;
    position: absolute;
    top: 396px;
    left: 0;
    font-weight: 300;
    font-size: 14px;
    line-height: 20px;
    color: #ffffff;
    content: "Click to copy to clipboard";
    letter-spacing: initial;
    padding: 4px 8px;
    border-radius: 32px;
    transition: all 0.2s;
}
.code_display:hover::before
{
    font-weight: 400;
    font-size: 16px;
    opacity: 100%;
    transition: all 0.2s;
}